import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default{
	components: {
		uniPopup
	},
	props: {
		shareSummary: {
			type: String,
			default: ''
		},
		shareImage: {
			type: String,
			default: ''
		},
		shareTitle: {
			type: String,
			default: ''
		},
		shareHref: {
			type: String,
			default: ''
		},
		miniProgram: {
			type: Object,
			default: function() {
				return {}
			}
		}
	},
	data() {
		return {
			bottomData: [{
					text: '微信',
					icon: '../../static/weixin.png',
					name: 'weixin'
				},
				{
					text: '朋友圈',
					icon: '../../static/pengyouquan.png',
					name: 'timeline'
				},
				{
					text: 'QQ',
					icon: '../../static/QQ.png',
					name: 'qq'
				},
				{
					text: '新浪',
					icon: '../../static/weibo.png',
					name: 'sinaweibo'
				},
				{
					text: '小程序',
					icon: '../../static/miniProgram.png',
					name: 'miniProgram'
				},
				{
					text: '其他',
					icon: '../../static/other.png',
					name: 'more'
				}
			],
		}
	},
	methods: {
		open() {
			this.$nextTick(()=>{
				this.$refs['showshare'].open()
			})
		},
		change(e) {
			// console.log('是否打开:' + e.show)
		},
		shareItems(provider){
			let shareOPtions = {
				provider: provider,
				summary: this.shareSummary,
				imageUrl: this.shareImage ? this.shareImage : '',
				href : this.shareHref,
				title : this.shareTitle,
				type : (this.shareImage && this.shareSummary) ? 0 : this.shareImage ? 2 : this.shareSummary ? 1 : '',
				success: (e) => {
					this.$emit('shareSuccess',e)
				},
				fail: (e) => {
					this.$emit('shareFail',e)
				},
			};
			if(shareOPtions.type === ''){
				uni.showModal({
					content:'请分享文字或者图片',
					showCancel:false
				})
				return;
			};
			switch (provider){
				case 'weixin':
					shareOPtions.scene = 'WXSceneSession';
					break;
				case 'timeline' :
					shareOPtions.provider = 'weixin';
					shareOPtions.scene = 'WXSenceTimeline';
					break;
				case 'qq' :
					shareOPtions.type = this.shareImage ? 2 : 1;
					break;
				case 'miniProgram' :
					shareOPtions.provider = 'weixin';
					shareOPtions.type = 5;
					shareOPtions.scene = 'WXSceneSession';
					shareOPtions.miniProgram = this.miniProgram;
					break;
				case 'more' :
					plus.share.sendWithSystem({
						type:"web",
						title:shareOPtions.shareTitle,
						thumbs:[shareOPtions.shareImage],
						href:shareOPtions.shareHref,
						content: shareOPtions.summary,
					})
				default:
					break;
			}
			if(shareOPtions.type === 0 && (!shareOPtions.imageUrl || !shareOPtions.href) && shareOPtions.provider == 'weixin'){
				uni.showModal({
					content:'分享图文必须有链接',
					showCancel:false
				})
				return;
			}else if(shareOPtions.type == 5 && (!shareOPtions.imageUrl || !shareOPtions.miniProgram)){
				uni.showModal({
					content:'小程序参数跟图片地址必填',
					showCancel:false
				})
				return;
			}else if((shareOPtions.provider == 'qq' && shareOPtions.type == 1) && !shareOPtions.shareTitle){
				uni.showModal({
					content:'分享文字到 QQ 时，title 必选',
					showCancel:false
				})
				return;
			}
			uni.share(shareOPtions)
		},
		cancel() {
			this.$refs['showshare'].close()
		}
	}
}
